function rand (from = 0, to = 1) {
  return from + Math.random() * (to - from);
}

rand.int = function (from, to) { return rand(from, to + 1) | 0 };
rand.hexColor = function () { return `#${ rand.int(0, 2**24).toString(16) }` };
rand.bool = function () { return Math.random() > .5 };

function smoothstep (min, max, value) {
  let x = (value-min)/(max-min);
  x = x < 0 ? 0 : x > 1 ? 1 : x;
  return x*x*(3 - 2*x);
}

function clamp (min, max, v) {
  return v < min ? min : v > max ? max : v;
}

const lerp = (x, y, a) => x * (1 - a) + y * a;
const invlerp = (x, y, a) => clamp(0, 1, (a - x) / (y - x));

function circularEaseOut (x) {
  return Math.sqrt(1 - (1 - x) ** 2);
}

function doubleExponentialSeat (x, a) {

  const epsilon = 0.00001;
  const min_param_a = 0.0 + epsilon;
  const max_param_a = 1.0 - epsilon;
  a = Math.min(max_param_a, Math.max(min_param_a, a)); 

  let y = 0;
  if (x<=0.5){
    y = (Math.pow(2.0*x, 1-a))/2.0;
  } else {
    y = 1.0 - (Math.pow(2.0*(1.0-x), 1-a))/2.0;
  }
  return y;
}

function doubleExponentialSigmoid (x, a){

  const epsilon = 0.00001;
  const min_param_a = 0.0 + epsilon;
  const max_param_a = 1.0 - epsilon;
  a = Math.min(max_param_a, Math.max(min_param_a, a));
  a = 1.0-a; // for sensible results
  
  let y = 0;
  if (x<=0.5){
    y = (Math.pow(2.0*x, 1.0/a))/2.0;
  } else {
    y = 1.0 - (Math.pow(2.0*(1.0-x), 1.0/a))/2.0;
  }
  return y;
}

export { circularEaseOut, clamp, doubleExponentialSeat, doubleExponentialSigmoid, invlerp, lerp, rand, smoothstep };
